body {
  margin: 0;
  padding: 0;
}

html {
  --active-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav {
  margin: 0 50px 0 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

li {
  margin-right: 20px;
}

li:last-child {
  margin-right: 0;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo__image {
  width: 40px;
  height: 40px;
  margin: 10px 0 10px 50px;
  display: block;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

a:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}
